<template>
  <div>
    <!-- <h3 class="title_container">采购明细表</h3> -->
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="采购组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" @focus="companyListInvok" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @focus="supplier" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="品牌名称" size="mini">
          <getBrand v-model='params.brand_id'  />
        </el-form-item>

        <el-form-item label="商品名称" size="mini">
          <el-input v-model="params.goods_name" clearable placeholder="请输入"></el-input>
        </el-form-item>

        <el-form-item label="出入库类型" size="mini">
          <el-select clearable v-model="params.type" placeholder="请选择">
            <el-option label="采购入库" value="1">采购入库</el-option>
            <el-option label="采购退货" value="2">采购退货</el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="仓库" size="mini">
          <el-select v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseInvok" @change="warehouseChangeId">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="货位" size="mini">
          <el-select v-model="params.allocation_name" filterable remote clearable @focus="InventoryInvok">
            <el-option v-for="item in allocationInventoryList" :key="item.allocation_name" placeholder="请选择" :label="item.allocation_name" :value="item.allocation_name">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.sn" clearable placeholder="请输入"></el-input>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="请选择" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="采购员" size="mini">
          <el-select v-model="params.buyer_id" filterable @focus="EmployeeListInvok" @change="businessIdChange">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet" v-if="authority.b_export==1 && listShow">
      <el-dropdown placement="bottom-start">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <!-- <div class="add_delet">
      <el-select clearable v-model="params.export_type" placeholder="请选择">
        <el-option label="CSV逗号分隔" value="2">CSV逗号分隔</el-option>
        <el-option label="Excel文件" value="1">Excel文件</el-option>
      </el-select>
      <el-button plain size="mini" icon="iconfont icon-daochu10" @click="exportOrder" class="exporT" v-if="authority.b_export==1">导出</el-button>
    </div> -->
    <div class="table_container" v-if="listShow">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :max-height="tableHeight">
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="采购组织" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="sn" label="单据号" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" :render-header="renderHeaderpro" property="production_time" width="80" label="生产日期">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="type_name" label="出入库类型" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="出入库仓库" width="132"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="出入库货位" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="采购数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购单价" align="right">
          <template slot-scope="scope">
            {{scope.row.purchase_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="采购金额" width="120" align="right">
          <template slot-scope="scope">
            {{scope.row.subtotal_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="buyer_name" label="采购员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="180"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="单据备注"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="g_remake" label="商品备注"></el-table-column>
      </el-table>
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div>采购数量总计：{{TableData.allList.all_num}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">采购金额总计：{{TableData.allList.all_price|priceFormatNone}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div>生成时间：{{TableData.allList.add_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">制表人：{{TableData.allList.add_user_name}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div>业务日期起始:{{this.params.start_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">业务日期起始:{{this.params.end_time}}</div>
          </el-col>
        </el-row>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import {
  getData,
  supplierListOpen, //获取供应商下拉
  tableResize, //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportPurchasedetrpt",
  data() {
    return {
      authority: {},
      timeValue2: [],
      supplierList: [],
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      export_type: null,
      companyList: {},
      customerList: {
        options: [],
      }, //客户列表
      TableData: {
        allList: {},
        tableData: [],
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      loading: true,
      per_page: [30, 50, 100, 500],
      brandsList: [], //品牌管理相关
      list: [], //品牌管理相关
      tableHeight: 600,
    };
  },

  methods: {
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    businessIdChange(val) {
      if (val != "") {
        this.params.buyer_name = this.employeeList.filter(
          (obj) => obj.employee_id === val
        )[0].employee_name;
      }
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    change2(val) {
      //选择的日期
      this.params.production_start_time = val[0];
      this.params.production_end_time = val[1];
    },
    supplierChange(val) {
      let options = {
        company_id: this.params.company_id,
        supplier_name: val,
      };
      supplierListOpen(this, options);
    },
    warehouseChangeId(val) {
      this.$set(this.params, "allocation_name", " ");
    },
    exportOrder(scheduleType) {
      // 导出
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error",
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportPurchaseDetailUrl(this.params).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    companyChange(val) {
      // 获取部门
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "department_id", "");
    },
    supplier() {
      let options = {
        company_id: this.params.company_id,
        supplier_name: null,
      };
      supplierListOpen(this, options);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getPurchaseDetailList", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams, 2);
    },
    onSearch() {
      //查询
      this.listShow = true;
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }

      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.production_start_time = this.timeValue2[0];
        this.params.end_production_end_timetime = this.timeValue2[1];
      } else {
        this.params.production_start_time = this.params.production_end_time =
          "";
      }

      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });

      getData(this.searchParams, 2, tableResize.bind(this));
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getPurchaseDetailList", //接口名
      vm: this, //传递的this
    });
    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  },
};
</script>
